import * as turf from '@turf/turf'

// /格式转换
export function pointsToDegreesArray(points) {
    console.log(points)
        let degreesArray = [];
        points.map(item => {
            degreesArray.push(item[0]);
            degreesArray.push(item[1]);
        });
        return degreesArray;
    }
// 添加turf
// along获取一个LineString并返回沿该线指定距离的点。
export function along(){
    var line = turf.lineString([
        [109.502991, 29.68718],
        [108.837829, 32.969237],
        [113.567871, 37.200787]
      ]);
    var options = {units: 'miles'};

    var along = turf.along(line, 300, options);
    return along;
}


// area获取一个或多个feature，并返回其面积平方米。
export function area(){
    var polygon = turf.polygon([[
        [108.09876, 37.200787],
        [106.398901, 33.648651],
        [114.972103, 33.340483],
        [113.715685, 37.845557],
        [108.09876, 37.200787]
      ]]);

    var area = turf.area(polygon);
    return area;
}

//计算边界 获取一组feature，计算所有feature的bbox，并返回一个边界框。
export function bbox(){
    var line = turf.lineString([
        [104.99467, 30.071677],
        [107.13797, 36.550462],
        [112.607082, 34.991467]
      ]);
    var bbox = turf.bbox(line);
    var bboxPolygon = turf.bboxPolygon(bbox);
    return bboxPolygon;
}

// 计算边界多边形

// 获取一个bbox并返回一个等价的多边形。
export function bboxPolygon(){
    var bbox = [105.361046, 35.356724, 111.59974, 30.934089]; // 左上右下经纬度

    var poly = turf.bboxPolygon(bbox);
    return poly;
}





