// 添加面
export function addpolygon(viewer){
    let obj = viewer.entities.add({
        name: '面',
        polygon: {
            hierarchy: {//层次
                positions: Cesium.Cartesian3.fromDegreesArray([
                    -99.0,
                    30.0,
                    -85.0,
                    30.0,
                    -85.0,
                    40.0,
                    -99.0,
                    40.0,
                ]),
            },
            material: Cesium.Color.RED.withAlpha(0.5)
        }
    });
    // viewer.scene.forceRender();
    viewer.scene.requestRender();
    return obj;
}


// 添加自定义面
export function cpolygon(viewer,arr){
    let obj = viewer.entities.add({
        name: '面',
        polygon: {
            hierarchy: {//层次
                positions: Cesium.Cartesian3.fromDegreesArray(arr),
            },
            material: Cesium.Color.RED.withAlpha(0.5)
        }
    });
    // viewer.scene.forceRender();
    viewer.scene.requestRender();
    return obj;
}


