<template>
   <div class="container">
        <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal">
        <el-menu-item index="1">
          <template slot="title">
            <el-select v-model="value" placeholder="选择添加点线面等" @change="change">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </template>
        </el-menu-item>

        <el-menu-item index="2">
          <template slot="title">
            <el-select v-model="turfv" placeholder="选择添加计算等" @change="changeturf">
              <el-option
                v-for="item in turfo"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </template>
        </el-menu-item>

        <el-menu-item index="2">
          <template slot="title">
            <el-select v-model="mouse" placeholder="选择添加鼠标事件等" @change="changemouse">
              <el-option
                v-for="item in mouseo"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </template>
        </el-menu-item>
      </el-menu>
  </div>
</template>

<script>
import utils from "../utils/index"
export default {
  name: 'HelloWorld',
  props:["viewer"],
  data () {
    return {
      msg: '',
      activeIndex:"0",
      options: [{
          value: '添加点',
          label: '添加点'
        }, {
          value: '添加面',
          label: '添加面'
        }, {
          value: '添加线',
          label: '添加线'
        },{
          value: '获取当前中心点',
          label: '获取当前中心点'
        },{
          value: '跳转到亚洲',
          label: '跳转到亚洲'
        }],
        turfo:[
        {
          value: 'along',
          label: 'along'
        }, {
          value: 'area',
          label: 'area'
        }, {
          value: 'bbox',
          label: 'bbox'
        },{
          value: '边界多边形',
          label: '边界多边形'
        }
        ],
        mouseo:[
        // {
        //   value: 'left',
        //   label: 'left'
        // }, {
        //   value: 'right',
        //   label: 'right'
        // }, {
        //   value: 'remove',
        //   label: 'remove'
        // },
        {
          value: '左键加点',
          label: '左键加点'
        },
        // {
        //   value: '关闭当前浏览器',
        //   label: '关闭当前浏览器'
        // },{
        //   value: '打开新的页面',
        //   label: '打开新的页面'
        // }
        ],
        mouse: '',
        value: '',
        turfv: '',
        entites:[]
    }
  },
   mounted() {

   },
   methods:{
    change(val){
      console.log(val)
      console.log(this.viewer)
      console.log(utils.getcenter(this.viewer))
      console.log(utils.along().geometry.coordinates)
      let center=utils.getcenter(this.viewer)
      if(val=="添加点"){
        utils.addpoint(this.viewer)
      }
      if(val=="添加面"){
        utils.addpolygon(this.viewer)
      }
      if(val=="添加线"){
        utils.addline(this.viewer)
      }
      if(val=="获取当前中心点"){

        this.$message({
          message: '中心点是:'+utils.tstring(center),
          type: 'success'
        });
      }

      if(val=="跳转到亚洲"){
        utils.fly(this.viewer)
      }

    },
    changeturf(val){

      if(val=="along"){
        let latlon=utils.along().geometry.coordinates
        utils.cpoint(this.viewer,latlon[0],latlon[1])
      }

      if(val=="area"){
        console.log("面积：",utils.area())
        this.$message({
          message: '面积是:'+utils.area()+"平方米。",
          type: 'success'
        });
      }

      if(val=="bbox"){
        let arr=utils.pointsToDegreesArray(utils.bbox().geometry.coordinates[0])
        utils.cpolygon(this.viewer,arr)
      }

      if(val=="边界多边形"){
        let arr=utils.pointsToDegreesArray(utils.bboxPolygon().geometry.coordinates[0])
        utils.cpolygon(this.viewer,arr)
      }


    },
    changemouse(val){
      if(val=="left"){
        utils.mouseL(this.viewer)
      }
      if(val=="remove"){
        utils.removemouse(this.viewer)
      }

      if(val=="左键加点"){
        let position=utils.mouseL(this.viewer)
        console.log(position)
        this.$message({
          message: '右键取消绘制！',
          type: 'success'
        });
        // utils.cpoint(this.viewer,position.latitude,position.longitude)
      }

      if(val=="关闭当前浏览器"){
        this.$message({
          message: '关闭打开的window浏览器！',
          type: 'success'
        });
        window.close()
        // let position=utils.mouseL(this.viewer)
        // console.log(position)
        // utils.cpoint(this.viewer,position.latitude,position.longitude)
      }

      if(val=="打开新的页面"){
        // window.open("#/")
        window.open("#/",'','height=900,width=1000,top=100,left=100')
        // let position=utils.mouseL(this.viewer)
        // console.log(position)
        // utils.cpoint(this.viewer,position.latitude,position.longitude)
      }
    }
   },




}
</script>

<style scoped>
.container{
  position: absolute;
  z-index: 10000;
}
.el-menu-demo{
  background: transparent;
}
</style>
<style>
.el-menu--horizontal>.el-menu-item{
  color: aliceblue !important;
}
.el-menu--horizontal>.el-menu-item:hover{
  background: transparent !important;
  color: aliceblue !important;
}
.el-input__inner{
  background: transparent !important;
  color: #fff !important;
}
</style>



