// 添加点
export function addpoint(viewer){
    let obj=viewer.entities.add({
        position: Cesium.Cartesian3.fromDegrees(-122.2058, 46.1955, 100000.0),
        point: {
            pixelSize: 10,
            color: Cesium.Color.YELLOW,
        },
        label: { //文字标签
          text: "文字标签",
          font: '500 30px Helvetica',// 15pt monospace
          scale: 0.5,
          style: Cesium.LabelStyle.FILL,
          fillColor: Cesium.Color.WHITE,
          pixelOffset: new Cesium.Cartesian2(-8, -35), //偏移量
          showBackground: true,
          backgroundColor: new Cesium.Color(0.5, 0.6, 1, 1.0)
          }
    });
    // viewer.scene.forceRender();
    viewer.scene.requestRender();
    return obj;
}

// 添加某个位置的点
export function cpoint(viewer,lat,lon){
    let obj=viewer.entities.add({
        position: Cesium.Cartesian3.fromDegrees(lon, lat, 100000.0),
        point: {
            pixelSize: 10,
            color: Cesium.Color.YELLOW,
        },
        label: { //文字标签
          text: "文字标签",
          font: '500 30px Helvetica',// 15pt monospace
          scale: 0.5,
          style: Cesium.LabelStyle.FILL,
          fillColor: Cesium.Color.WHITE,
          pixelOffset: new Cesium.Cartesian2(-8, -35), //偏移量
          showBackground: true,
          backgroundColor: new Cesium.Color(0.5, 0.6, 1, 1.0)
          }
    });
    // viewer.scene.forceRender();
    viewer.scene.requestRender();
    return obj;
}


