<template>
   <div class="container">
     <Menu :viewer="viewer"></Menu>
     <el-tag class="bigtag">Cesium体验</el-tag>
    <div id="cesiumContainer"></div>
  </div>
</template>

<script>
import Menu from './Menu.vue';
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: '',
      viewer:''
    }
  },
  components:{
    Menu:Menu
  },
   mounted() {
    const viewer = new Cesium.Viewer("cesiumContainer",{
      // baselayer:false,//默认底图不要加载
      geocoder: false,                //是否显示地名查找控件
            sceneModePicker: false,         //是否显示投影方式控件
            navigationHelpButton: false,    //是否显示帮助信息控件
            baseLayerPicker: false,         //是否显示图层选择控件
            homeButton: false,              //是否显示Home按钮
            fullscreenButton: false,        //是否显示全屏按钮    
            timeline:false,                 //时间轴控件
            animation:true,                //动画控件
            maximumRenderTimeChange: Infinity, // 无操作时自动渲染帧率，设为数字会消耗性能，Infinity为无操作不渲染
            scene3DOnly: true, // 是否只使用3d渲染（为true时可优化性能）
            targetFrameRate: 10,  // 帧率
            requestRenderMode: true, //启用请求渲染模式 
            useBrowserRecommendedResolution: false, // 是否选择浏览器推荐分辨率
            // imageryProvider: new Cesium.ArcGisMapServerImageryProvider({
            //   url: "http://map.geoq.cn/ArcGIS/rest/services/ChinaOnlineStreetPurplishBlue/MapServer",
            //   //url: "https://services.arcgisonline.com/arcgis/rest/services/World_Imagery/MapServer",
            // }),//给一个默认底图
      });
      viewer.scene.globe.depthTestAgainstTerrain = true; //开启深度检测
      viewer.cesiumWidget.creditContainer.style.display = "none";//去cesium logo水印 或 css
      // 隐藏太阳和月亮

      viewer.scene.sun.show = false;
      viewer.scene.moon.show = false;

      // 关闭天空盒，否则会显示天空颜色
      viewer.scene.skyBox.show = false
      // 开启地下模式，设置基色透明，隐藏褐色地球
      viewer.scene.undergroundMode = true
      viewer.scene.debugShowFramesPerSecond = true;
      // viewer.scene.globe.show = false; 
// viewer.scene.globe.baseColor = new Cesium.Color(0, 0, 0, 0);
// viewer.scene.backgroundcolor = new Cesium.Color(0, 0, 0, 0);
      this.viewer=viewer;
   }



}
</script>

<style scoped>
.bigtag{
  position: absolute;
    right: 0.6rem;
    z-index: 10000;
    top: 3rem;
    height: 4rem;
    font-size: 20px;
    line-height: 4rem;
    color: red;
}
</style>



