import { cpoint } from "./point";
// 鼠标左键
export function mouseL(viewer){
    var handler = new Cesium.ScreenSpaceEventHandler(viewer.scene.canvas);
    let postion="",coordinate=""
    handler.setInputAction(function(click){
        console.log('左键单击事件：',click.position);
        postion= click.position;
        let cartesian = viewer.camera.pickEllipsoid(click.position);
        let cartographic = Cesium.Cartographic.fromCartesian(cartesian);
        let lng = Cesium.Math.toDegrees(cartographic.longitude); // 经度
        let lat = Cesium.Math.toDegrees(cartographic.latitude); // 纬度
        let alt = cartographic.height; // 高度，椭球面height永远等于0
        coordinate = {
            longitude: Number(lng.toFixed(6)),
            latitude: Number(lat.toFixed(6)),
            altitude: Number(alt.toFixed(2))
        };
        cpoint(viewer,lat,lng)
        console.log(coordinate);
    },Cesium.ScreenSpaceEventType.LEFT_CLICK);

    // 右键停止绘制
    handler.setInputAction(function () {
        handler.destroy();//关闭事件句柄
        handler = null;
      }, Cesium.ScreenSpaceEventType.RIGHT_CLICK);

    return coordinate;
}

// 鼠标右键
export function mouseR(viewer){
    var handler = new Cesium.ScreenSpaceEventHandler(viewer.scene.canvas);
    let postion=""
    handler.setInputAction(function(click){
        console.log('右键单击事件',click.position);
        postion= click.position;
    },Cesium.ScreenSpaceEventType.RIGHT_CLICK);

    return postion;
}

// 鼠标删除事件

export function removemouse(viewer){
    console.log('移除单击事件');
    var handler = new Cesium.ScreenSpaceEventHandler(viewer.scene.canvas);
    console.log(handler)
    handler.removeInputAction(Cesium.ScreenSpaceEventType.LEFT_CLICK);
    handler.removeInputAction(Cesium.ScreenSpaceEventType.RIGHT_CLICK);

        // 左键双击停止绘制
        handler.setInputAction(function () {
            handler.destroy();//关闭事件句柄
            handler = null;
          }, Cesium.ScreenSpaceEventType.LEFT_DOUBLE_CLICK);
          // 右击单击停止绘制
          handler.setInputAction(function () {
            handler.destroy();//关闭事件句柄
            handler = null;
          }, Cesium.ScreenSpaceEventType.RIGHT_CLICK);
    return '';
}



